import React,{useState} from 'react' 
import {addTodo, deleteTodo, removeTodo} from '../actions/index'
import {useSelector, useDispatch } from 'react-redux'


const Todo = () => {
    const [inputdata, setinputdata] = useState('')
    const dispatch = useDispatch()
    const list = useSelector((state) => state.todoReducers.list)
    return (
        <>
            <div className="main-div">
                <div className="child-div">
                    <figure>
                        <figcaption>Add Your List Here</figcaption>
                    </figure>

                    <div className="addItems">
                        <input type="text" placeholder="Add Items..." value={inputdata} onChange={(event) => setinputdata(event.target.value)}/>
                        <i class="far fa-plus-square plus-btn add-btn" onClick = {() => {dispatch(addTodo(inputdata));setinputdata('')}} ></i>
                    </div>

                    <div className="showItems">
                        {
                            list.map((e) => {
                                return(
                                    <div className="eachItem" key={e.id}>
                                        <h3>{e.data}</h3>
                                        <div className="todo-btn">
                                            <i className="far fa-trash-alt add-btn" title="
                                            Delete Item" onClick={() => dispatch(deleteTodo(e.id))}></i>
                                        </div>
                                    </div>
                                )
                            })
                        }
                        
                    </div>


                    <div className="show-Items">
                        <button className="btn" onClick={() => dispatch(removeTodo())}>Remove All</button>
                    </div>
                </div>
            </div>
        </>
    )
}

export default Todo
